import React from 'react';
import { Link } from 'react-router-dom'

import { serverResponse, getContainer } from '../Func/request';
import '../style.css';


class Map extends React.Component {
	constructor (props) {
		super(props)
		this.state = {
            user: { id: null, name: null },
			popup: { active: false, current: null },
            center: [30.3165, 59.9392],
            namesContainers: ['container_01','container_02','container_03','container_04','container_05',],
			сontainer: []
		}
		this.onUpdateMap = this.onUpdateMap.bind(this);
		this.onAuth = this.onAuth.bind(this);
	}

    componentDidMount() {
		this.onUpdateMap()
    }

	onAuth(_id,_name) {
		this.setState({ user: { id: _id, name: _name } });
	}

	onPopup(_active, _current) {
		this.setState({ popup: { active: _active, current: _current } });
	}

	onUpdateMap() {
        var mapboxgl = require('mapbox-gl/dist/mapbox-gl.js');
        mapboxgl.accessToken = 'pk.eyJ1IjoibWlrZS1wZXRyb3YiLCJhIjoiY2p4MW1idnI2MGFnczQ5cWZtcHExYXVucSJ9.R5eaAB5PePMxt7dM9PEFqg';
        var map = new mapboxgl.Map({
            container: 'mapbox',
            style: 'mapbox://styles/mapbox/streets-v11',
            center: this.state.center,
			zoom: 12
        });

		document.getElementById('btn-now').addEventListener('click', function() {
			// map fly
			map.flyTo({
				center: [30.3165, 59.9392],
				zoom: 15
			});

		});
	}

    render() {
        return (
			<div className="map">
				<div id="panel">
					{this.state.user.id === null ?
						<React.Fragment>
							<div onClick={()=>{this.onAuth('1','Mike')}}>
								<i className="fas fa-sign-in-alt"></i>
							</div>
							<div id="btn-now">
								<i className="fas fa-location-arrow"></i>
							</div>
						</React.Fragment>
						:
						<React.Fragment>
							<div onClick={()=>{this.onPopup(true,'profile')}}>
								<i className="fas fa-user"></i>
							</div>
							<div id="btn-now">
								<i className="fas fa-location-arrow"></i>
							</div>
							<div onClick={()=>{this.onPopup(true,'box')}}>
								<i className="fas fa-boxes"></i>
							</div>
							<div onClick={()=>{this.onAuth(null,null)}}>
								<i className="fas fa-sign-out-alt"></i>
							</div>
						</React.Fragment>
					}
				</div>
				<div id="mapbox"></div>
				<div id="package">
					<div>
						<img src="./img/scooter.png" />
					</div>
					<div className="scooter-info">
						<div id="scooter-number">
							<i className="fas fa-hashtag"></i> 225403
						</div>
						<div id="scooter-name">
							Xiaomi
						</div>
						<div id="scooter-energy">
							<i className="fas fa-bolt"></i> 59 %
						</div>
					</div>
					<div id="scooter-play">
						START
					</div>
				</div>
             </div>
        );
    }
}

export default Map;
